//Form validation

const form = document.getElementById('form');
const guestName = document.getElementById('name');
const guestEmail = document.getElementById('email');
const month = document.getElementById('month');
const day = document.getElementById('day');
const year = document.getElementById('year');
const hour = document.getElementById('hour');
const minutes = document.getElementById('minutes');


form.addEventListener('submit', (e) => {
    e.preventDefault();
    checkInputs();
});

function checkInputs() {
    const guestNameValue = guestName.value;
    const guestEmailValue = guestEmail.value.trim();
    const monthValue = month.value.trim();
    const dayValue = day.value.trim();
    const yearValue = year.value.trim();
    const hourValue = hour.value.trim();
    const minutesValue = minutes.value.trim();

    if(guestNameValue === '') {
        setErrorFor(guestName, 'This field is required');
    }

    if(guestEmailValue === '') {
        setErrorFor(guestEmail, 'This field is required');
    }

    if(monthValue === '') {
        setErrorFor(month, 'This field is incomplete');
    }

    if(dayValue === '') {
        setErrorFor(day, 'This field is incomplete');
    }

    if(yearValue === '') {
        setErrorFor(year, 'This field is incomplete');
    }

    if(hourValue === '') {
        setErrorFor(hour, 'This field is incomplete');
    }

    if(minutesValue === '') {
        setErrorFor(minutes, 'This field is incomplete');
    }

}

function setErrorFor(input, message) {
    const formControl = input.parentElement; 
    const small = formControl.querySelector('small');

    small.innerText = message;

    formControl.classList.add('error');
}

// Time options

const selected = document.querySelector('.choose-time-opt__options__selected');
const optionsContainer = document.querySelector('.choose-time-opt__options__container');
const chooseOption = document.querySelectorAll('.choose-time-opt__options__container__option');

selected.addEventListener('click', () => {
    optionsContainer.classList.toggle('active');
});

chooseOption.forEach( o => {
    o.addEventListener('click', () => {
        selected.innerHTML = o.querySelector('label').innerHTML;
        optionsContainer.classList.remove('active');
    });
});

// Counter 

const decreaseBtn = document.getElementById('btn__decrease');
const increaseBtn = document.getElementById('btn__increase');
const number = document.getElementById('number');
let newNumber = 0;

decreaseBtn.addEventListener('click', () => {
    newNumber --;
    number.innerText = newNumber;
});

increaseBtn.addEventListener('click', () => {
    newNumber ++;
    number.innerText = newNumber;
});